*{
    padding: 0;
    margin: 0;
}
#head{
    margin: 0 auto;
    height:50px;
    background: rgb(2, 2, 2);
}
#header{
    margin: 0 auto;
    width:1000px;
    height:50px;
    background: rgb(2, 2, 2);
}
#title{
    line-height:50px;
    color: white;
    font-size:30px;
}
#get{
    float: right;
    height:20px;
    margin-top:12px;
    width:300px;
    border-radius:5px;
}
#border{
    margin: 0 auto;
    height:500px;
    width:800px;
    
   
}
#listborder{
    background-color: rgb(167, 164, 164);
}

ul{
    list-style-type: none;
}

button{
    float: right;
    margin-right: 10px;
    width:20px;
    height:20px;
    border-radius:10px;
    margin-top: 5px;
}


#add li{
    width:800px;
    height:30px;
    background-color: white;
    margin-bottom:8px;
    border-radius:3px;
}


#end li{
    width:800px;
    height:30px;
    background-color: rgb(196, 190, 190);
    margin-bottom:8px;
    border-radius:3px;
}

#number{
    width:20px;
    height:20px;
    border-radius: 10px;
    float: right;
    margin-right: 10px;
    text-align: center;
    border-radius:10px;
    background-color: white;
}
#buttom{
    width:20px;
    height:20px;
    float: left;
    margin-left: 10px;
    background-color: rgb(167, 164, 164);
    margin-top: 5px;
}